@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@900&family=Poppins:wght@300;400;500;600;700;800&family=Montserrat:wght@500;600;700;800&display=swap');

body{
  background: #f1f2f6;
}

.toggle-btn{
  font-size: 30px;
}

.custom-bg{
  background-color: #f1f2f6;
  background-image: url("data:image/svg+xml,%3Csvg width='52' height='26' viewBox='0 0 52 26' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23dfe4ea' fill-opacity='0.4'%3E%3Cpath d='M10 10c0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6h2c0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4v2c-3.314 0-6-2.686-6-6 0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6zm25.464-1.95l8.486 8.486-1.414 1.414-8.486-8.486 1.414-1.414z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

h3{
  font-family: 'Poppins', Sans-Serif;
  font-weight: 600;
}

h4{
  font-family: 'Montserrat', Sans-Serif;
  font-weight: 600;
}

p{
  font-family: 'Montserrat', Sans-Serif;
  font-weight: 500;
  font-size: 0.9em;
}

b{
  font-family: 'Montserrat', Sans-Serif;
  font-weight: 700;
  font-size: 1em;
}

.text-orange{
  color: #F44336;
}
.text-darkblue{
  color: #3F51B5;
}
.text-lightblue{
  color: #70a1ff;
}

.bg-customblue{
  background: rgba( 74, 144, 226, 0.25 );
  backdrop-filter: blur( 20px );
  -webkit-backdrop-filter: blur( 20px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );  display: inline-block;
  font-family: 'Poppins', Sans-Serif;
  font-weight: 500;
  font-size: 0.9em;
  color: #3F51B5;
}

.bg-customlight{
  background: rgba( 206, 214, 224, 0.25 );
  backdrop-filter: blur( 20px );
  -webkit-backdrop-filter: blur( 20px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );  display: inline-block;
  font-family: 'Poppins', Sans-Serif;
  font-weight: 500;
  font-size: 0.9em;
  color: #3F51B5;
}

.tab-menu{
  font-family: 'Poppins', Sans-Serif;
  font-weight: 500;
  color: #fff;
  width: 115px;
  text-align: center;
  letter-spacing: 2px;
}

.heading-bg{
  background: url('./img/heading-bg.png');
  background-position: left;
  background-size: cover;
  padding-left: 9px;
}

.heading-border{
  border-bottom: 2px solid rgba( 74, 144, 226, 0.25 );
  display: inline-block;
}

.map-background{
  background: url('./img/map.png');
  background-size: cover;
  
}

.timeline{
  border-left: 1px solid rgba( 74, 144, 226, 0.25 );
}

.bg-customblue .icon{
  font-size: 1.6em;
  padding: 2px 13px 10px 13px;
}

#my-profesion{
  display: inline-block;
}

.profesion-title{
  padding-top: 1px;
  font-family: 'Poppins', Sans-Serif;
  font-weight: 500;
  color: #3F51B5;
  padding-left: 10px;
}

.profesion-desc{
  font-family: 'Poppins', Sans-Serif;
  font-weight: 500;
  font-size: 0.8em;
  color: #2f3542;
  opacity: 0.8;
  padding-left: 10px;
}